import { useSdkStore } from '@cetus/sdk-factory'
import { useAccountStore } from '@cetus/stores'
import useClmmSDKStore from '@cetus/stores/src/useClmmSDKStore'
import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'
import useRpcConfigStore from '@cetus/stores/src/useRpcConfigStore'
import useWebConfigStore from '@cetus/stores/src/useWebConfigStore'
import envConfigs from '@cetus/types/src/config/envConfigs'
import { AggregatorClient, Env } from '@cetusprotocol/aggregator-sdk'
import { SuiClient } from '@mysten/sui/client'
import VolatileVaultsSDK from 'haedal-vault-sdk'
import { useEffect } from 'react'
/**
 * SDK 初始化 统一在这个类中处理
 */
export function useInitSDK() {
  const { setAggregatorSDK, setIsInitLoading } = useClmmSDKStore()
  const { volatileVaultsSdk, setVolatileVaultsSdk } = usePeripherySDKStore()

  const { currentNode } = useRpcConfigStore()
  const { currentAccount } = useAccountStore()
  const { providers } = useWebConfigStore()
  const { setCurrentRpc, setAccount, setProviders } = useSdkStore()

  useEffect(() => {
    if (currentNode.link) {
      console.log('🚀 ~ useEffect ~ currentNode.link:', currentNode.link)
      setCurrentRpc(currentNode.link)
    }
  }, [currentNode.link])

  useEffect(() => {
    setAccount(currentAccount?.address)
  }, [currentAccount?.address])

  /**
   * 初始化aggregatorSDK 和 VolatileVaultsSDK
   */
  useEffect(() => {
    if (currentNode.link && currentAccount?.address) {
      setIsInitLoading(true)
      const newAggregatorClient = new AggregatorClient({
        endpoint: envConfigs.aggregatorUrl,
        signer: currentAccount?.address || '',
        client: new SuiClient({ url: currentNode.link }),
        env: envConfigs.env === 'testnet' ? Env.Testnet : Env.Mainnet,
        pythUrls: envConfigs.pythUrls
      })

      setAggregatorSDK(newAggregatorClient)

      const { sdkOptions: volatileVaultsSdkOptions } = volatileVaultsSdk
      volatileVaultsSdkOptions.fullRpcUrl = currentNode.link
      const newVolatileVaultsSdk = new VolatileVaultsSDK(volatileVaultsSdkOptions)
      newVolatileVaultsSdk.senderAddress = currentAccount?.address || ''
      setVolatileVaultsSdk(newVolatileVaultsSdk)

      setIsInitLoading(false)
    }
  }, [currentNode.link, currentAccount?.address])

  useEffect(() => {
    const { sdkOptions: volatileVaultsSdkOptions } = volatileVaultsSdk
    volatileVaultsSdkOptions.providers = providers.length > 1 ? providers : []
    const newVolatileVaultsSdk = new VolatileVaultsSDK(volatileVaultsSdkOptions)
    newVolatileVaultsSdk.senderAddress = volatileVaultsSdk.senderAddress
    setVolatileVaultsSdk(newVolatileVaultsSdk)

    setProviders(providers)
  }, [providers])
}
