import useSwapWidgetStore from '@/store/swap-widget/swapWidget'
import { SwapRouterFormat } from '@/types/swap'
import { useGetToken } from '@cetus/hooks/src/useToken'
import { CoinType } from '@cetus/types'
import { Box, HStack, Text, VStack } from '@chakra-ui/react'
import { useDeepCompareEffect } from 'ahooks'
import { useEffect, useMemo, useRef, useState } from 'react'
import H5Routes from '../swap/SwapRoutes/H5Routes'
import OverView from '../swap/SwapRoutes/OverView'
import SwapWidgetBack from './SwapWidgetBack'

type SwapWidgetRoutesProps = {
  data: SwapRouterFormat
  onClose: () => void
}

export default function SwapWidgetRoutes(props: SwapWidgetRoutesProps) {
  const { onClose, data } = props
  const { fromCoin, toCoin, findRouterLoading, fromAmount, toAmount } = useSwapWidgetStore()
  const { getTokenListInfo } = useGetToken()
  const [tokenMap, setTokenMap] = useState<Map<string, any>>(new Map())

  const getTokenMap = async (coinTypeList: string[]) => {
    const res = await getTokenListInfo(coinTypeList as CoinType[])
    if (res && res?.size > 0) {
      setTokenMap(res)
    }
  }

  useDeepCompareEffect(() => {
    if (data?.routers && data?.routers?.length > 0) {
      const coinTypeList: string[] = []
      data?.routers?.forEach(router => {
        router.paths.forEach(path => {
          if (!coinTypeList.includes(path.from_type)) {
            coinTypeList.push(path.from_type)
          }
          if (!coinTypeList.includes(path.to_type)) {
            coinTypeList.push(path.to_type)
          }
        })
      })
      getTokenMap(coinTypeList)
    }
  }, [data?.routers])

  const routes = useMemo(() => {
    if (!data?.routers || data?.routers?.length === 0 || tokenMap?.size === 0) {
      return []
    }
    return data?.routers?.map(router => ({
      ...router,
      paths: router.paths.map(path => ({
        ...path,
        fromToken: tokenMap?.get(path.from_type),
        toToken: tokenMap?.get(path.to_type)
      }))
    }))
  }, [data?.routers, tokenMap])

  const props2 = {
    fromToken: fromCoin,
    toToken: toCoin,
    fromAmount,
    toAmount,
    routes
  }

  const [loading, setLoading] = useState(true)
  const timer = useRef<any>(null)
  useEffect(() => {
    if (timer.current) {
      clearTimeout(timer.current)
      timer.current = null
    }

    timer.current = setTimeout(() => {
      setLoading(false)
    }, 1500)

    return () => {
      if (timer.current) {
        clearTimeout(timer.current)
        timer.current = null
      }
    }
  }, [])

  return (
    <VStack w="100%" gap="12px">
      <SwapWidgetBack title="Route" onBackClick={onClose} />

      <HStack w="100%" justify="space-between" p="0 12px">
        <Text fontSize="14px" color="text_caption" fontWeight="500" whiteSpace="nowrap">
          Router
        </Text>
        <Box>
          <OverView data={data} loading={findRouterLoading} />
        </Box>
      </HStack>
      <Box pl="12px" pr="12px" w="100%">
        <H5Routes {...props2} loading={loading} />
      </Box>
    </VStack>
  )
}
