import useRpcConfigStore from '@cetus/stores/src/useRpcConfigStore'
import { defaultNodes } from '@cetus/types'
import { RpcNode } from '@cetus/types/src/common-types'
import envConfigs from '@cetus/types/src/config/envConfigs'
import { fetchGet } from '../../utils/src/apis'

export default function useGetFrontConfig() {
  const { setNodeList } = useRpcConfigStore()

  const fetchFrontConfig = async () => {
    try {
      const url = `${envConfigs?.cetus_api}/v3/sui/frontend/config`
      const res = await fetchGet(url)
      const rpcNodeList = res.sui_nodes as RpcNode[]
      if (rpcNodeList.length > 0) {
        setNodeList(rpcNodeList)
      }
    } catch (error) {
      console.log('🚀 ~ file: useGetFrontConfig.ts:26 ~ fetchFrontConfig ~ error:', error)
      // 出现异常 取默认RPC 节点
      setNodeList([...defaultNodes])
      return null
    }
  }

  return {
    fetchFrontConfig
  }
}
